<template>
  <div class="header-container">
    <div class="logo">
      <img src="../../../public/img/logo11.png" alt="" />
    </div>
    <ul class="nav-tag">
      <li class="tag-item bold" @click="toPlace(0)">首页</li>
      <li class="tag-item thin" @click="toPlace(1)">关于</li>
      <li class="tag-item thin" @click="toPlace(2)">荣誉</li>
      <li class="tag-item thin" @click="toPlace(3)">加入我们</li>
    </ul>
  </div>
</template>

<script>
let body = document.querySelector('body')
export default {
  name: 'Header',
  data() {
    return {}
  },
  methods: {
    fixHeader() {
      let header = document.querySelector('.header-container')
      let top = document.documentElement.scrollTop || document.body.scrollTop
      console.log(top)
      if (top >= 300) {
        header.classList.remove('default-header')
        header.classList.add('fixed-header')
      } else {
        header.classList.remove('fixed-header')
        header.classList.add('default-header')
      }
    },
    lightTags() {
      let tags = document.querySelectorAll('.tag-item')
      let top = document.documentElement.scrollTop || document.body.scrollTop
      // 800 1355
      console.log(tags)
      if (top <= 220.8) {
        this.changeClass(tags, 0)
      } else if (top <= 960) {
        this.changeClass(tags, 1)
      } else if (top <= 1981.6) {
        this.changeClass(tags, 2)
      } else {
        this.changeClass(tags, 3)
      }
    },
    changeClass(a, n) {
      for (let i = 0; i < a.length; i++) {
        a[i].classList.remove('bold')
        a[i].classList.add('thin')
      }
      a[n].classList.remove('thin')
      a[n].classList.add('bold')
    },
    toPlace(val) {
      if (val === 0) {
        body.style.animation
        document.documentElement.scrollTop = 0
        document.body.scrollTop = 0
      } else if (val === 1) {
        document.documentElement.scrollTop = 573.6
        document.body.scrollTop = 573.6
      } else if (val === 2) {
        document.documentElement.scrollTop = 1676.8
        document.body.scrollTop = 1676.8
      } else {
        document.documentElement.scrollTop = 3313.6
        document.body.scrollTop = 3313.6
      }
    },
  },
  mounted() {
    window.onscroll = () => {
      this.fixHeader()
      this.lightTags()
    }
  },
}
</script>

<style lang="less" scoped>
.header-container {
  display: flex;
  min-width: 960px;
  max-width: 2000px;
  justify-content: space-around;
  width: 100%;
  height: 85px;
  margin-top: 15px;
  transition: all 1s;
}
.logo {
  img {
    height: 85px;
    vertical-align: middle;
    filter: invert(100%);
  }
}
.nav-tag {
  display: flex;
  align-items: flex-end;
  text-align: center;
  li {
    padding: 10px 15px;
    font-size: 20px;
    cursor: pointer;
    margin-bottom: -5px;
    border-bottom: 5px solid transparent;
    transition: all 0.5s;

    a {
      text-decoration: none;
    }
  }
  .bold {
    font-weight: 700;
    border-bottom: 5px solid #55aaff;
    color: #55aaff;
  }
  .thin {
    font-weight: 400;
    border-bottom: 5px solid transparent;
    color: #333;
  }
}
.fixed-header {
  position: fixed;
  top: 0;
  margin-top: 0 !important;
  background-color: #fff;
  box-shadow: 0px 3px 15px 0.5px rgba(200, 200, 200, 0.8);
  border-bottom: 2px solid rgba(150, 150, 150, 0.5);
  z-index: 999;
}
.default-header {
  margin-top: 10px;
  background-color: transparent;
}
</style>
